@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;300&family=Work+Sans:wght@100;300&display=swap");
@font-face {
  font-family: "Bruno Ace";
  src: url("../../fonts/BrunoAce/BrunoAceRegular.eot"); /* IE9 Compat Modes */
  src: url("../../fonts/BrunoAce/BrunoAceRegular.woff2") format("woff2"), url("../../fonts/BrunoAce/BrunoAceRegular.woff") format("woff"), url("../../fonts/BrunoAce/BrunoAceRegular.ttf") format("truetype"), url("../../fonts/BrunoAce/BrunoAceRegular.svg") format("svg"); /* Legacy iOS */
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
}

body {
  margin: 0;
  font-family: "Josefin Sans", sans-serif;
  font-size: 42px;
}
body .bg {
  animation: slide 12s ease-in-out infinite alternate;
  background-image: linear-gradient(60deg, #f2662c 50%, #141617 50%);
  bottom: 0;
  left: -50%;
  opacity: 0.5;
  position: fixed;
  right: -50%;
  top: 0;
  z-index: -1;
}
body .bg2 {
  animation-direction: alternate-reverse;
  animation-duration: 16s;
}
body .bg3 {
  animation-duration: 20s;
}

html {
  background-color: #0a090b;
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23512f0f' fill-opacity='0.19' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 7rem;
  scrollbar-width: thin;
  scrollbar-color: #f2662c #000;
}
html::-webkit-scrollbar-thumb {
  background-color: #f2662c;
}
html::-webkit-scrollbar-thumb:hover {
  background: #424242;
}
html::-webkit-scrollbar {
  width: 5px;
}
html::-webkit-scrollbar-track {
  background: #000;
}
@media (max-width: 599px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 900px) {
  html {
    font-size: 52%;
  }
}

section {
  min-height: 100vh;
  padding: 5rem 10%;
}
@media (min-width: 1800px) {
  section {
    padding: 3rem 5%;
  }
}
@media (max-width: 900px) {
  section {
    padding: 3rem 2rem;
  }
}

@keyframes slide {
  0% {
    transform: translateX(-25%);
  }
  100% {
    transform: translateX(25%);
  }
}
@keyframes swing-in-right-fwd {
  0% {
    transform: rotateY(-100deg);
    transform-origin: right;
    opacity: 0;
  }
  100% {
    transform: rotateY(0);
    transform-origin: right;
    opacity: 1;
  }
}
@keyframes slide-out-right {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(1000px);
    opacity: 0;
  }
}
@keyframes swing-in-top-fwd {
  0% {
    transform: rotateX(-100deg);
    transform-origin: top;
    opacity: 0;
  }
  100% {
    transform: rotateX(0deg);
    transform-origin: top;
    opacity: 1;
  }
}
@keyframes slide-out-bck-top {
  0% {
    transform: translateZ(1) translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateZ(-1100px) translateY(-1000px);
    opacity: 0;
  }
}
@keyframes hue-rotate {
  from {
    -webkit-filter: hue-rotate(0);
    -moz-filter: hue-rotate(0);
    -ms-filter: hue-rotate(0);
    filter: hue-rotate(0);
  }
  to {
    -webkit-filter: hue-rotate(360deg);
    -moz-filter: hue-rotate(360deg);
    -ms-filter: hue-rotate(360deg);
    filter: hue-rotate(360deg);
  }
}
.heading-title {
  font-family: "Bruno Ace";
  text-align: center;
  color: #fff !important;
  margin-bottom: 3rem;
  font-family: "Bruno Ace";
  font-size: 4rem;
  text-transform: capitalize;
}
.heading-title span {
  font-family: "Bruno Ace";
  color: #f2662c;
}

.btn {
  padding: 1rem 3rem;
  background-color: transparent;
  font-size: 1.7rem;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
  border: 0 solid;
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
  outline: 1px solid;
  outline-color: rgba(255, 255, 255, 0.5);
  outline-offset: 0px;
  text-shadow: none;
  transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
  background: rgba(255, 255, 255, 0);
  border-bottom: 1px solid;
  color: #fff;
  line-height: 1.4;
  padding: 0.25em;
  text-decoration: none;
}
.btn:hover {
  border: 1px solid;
  box-shadow: inset 0 0 20px rgba(73, 42, 1, 0.4), 0 0 20px rgba(87, 74, 43, 0.636);
  outline-color: rgba(255, 255, 255, 0);
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
  scale: 1.1;
  background: #f2662c;
  color: #fff;
}

.contact-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 34rem;
  background: #141617;
  height: 100%;
  text-align: center;
  z-index: 1100;
  padding: 0 24px;
  padding-top: 48px;
  display: none;
  -webkit-animation: swing-in-right-fwd 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  animation: swing-in-right-fwd 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.6);
}
.contact-menu.active {
  box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.45);
  display: block;
}
.contact-menu.in-active {
  display: block;
  animation: slide-out-right 0.2s linear both;
}
.contact-menu #close-contact-info {
  position: absolute;
  top: 4rem;
  right: 6rem;
  cursor: pointer;
  font-size: 32px;
  color: #f2662c;
  transition: all 0.2s cubic-bezier(0.1, 0.6, 0.2, 0.5);
}
.contact-menu #close-contact-info:hover {
  transform: rotate(90deg);
}
@media (max-width: 900px) {
  .contact-menu #close-contact-info {
    font-size: 24px;
  }
}
.contact-menu__content {
  padding: 8px 16px;
  margin-top: 40px;
}
.contact-menu__content .info {
  padding: 8px 0;
}
.contact-menu__content .info i {
  height: 40px;
  width: 40px;
  font-size: 24px;
  color: #f2662c;
  cursor: pointer;
  text-align: center;
  margin-bottom: -8px;
}
@media (max-width: 900px) {
  .contact-menu__content .info i {
    margin-bottom: -40px;
  }
}
.contact-menu__content .info p {
  font-size: 24px;
  line-height: 2;
  font-family: "Work Sans", sans-serif;
  letter-spacing: 2px;
  cursor: pointer;
  color: #fff;
  position: relative;
  transition: border-color 0.4s ease-in-out;
}
@media (max-width: 900px) {
  .contact-menu__content .info p {
    font-size: 16px;
    line-height: 2;
  }
}
.contact-menu__content .info p::after {
  content: "";
  position: absolute;
  border-top: 2px solid rgba(242, 102, 44, 0.2);
  left: 35%;
  bottom: 0;
  width: 30%;
  height: 0px;
  transition: all 0.2s ease-in-out;
}
.contact-menu__content .info p:hover::after {
  border-top: 2px solid rgba(242, 102, 44, 0.8);
  width: 32%;
  left: 34%;
}
.contact-menu__content .share {
  padding-top: 5rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
}
.contact-menu__content .share a {
  height: 70px;
  width: 70px;
  font-size: 32px;
  color: #f2662c;
  cursor: pointer;
  text-align: center;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  border-radius: 24px;
  color: rgba(242, 102, 44, 0.85);
  background: linear-gradient(145deg, #151819, #121415);
  box-shadow: 8px 8px 16px #090a0a, -8px -8px 16px #1f2224;
  border: 2px solid rgba(32, 33, 32, 0.12);
}
.contact-menu__content .share a::before {
  align-self: center;
}
.contact-menu__content .share a:hover {
  color: #f2662c;
  background: #141617;
  box-shadow: inset 8px 8px 16px #090a0a, inset -8px -8px 16px #1f2224;
  border: 2px solid rgba(32, 33, 32, 0.55);
}
@media (max-width: 900px) {
  .contact-menu__content .share a {
    height: 48px;
    width: 48px;
    font-size: 24px;
    color: #f2662c;
    cursor: pointer;
    text-align: center;
  }
}
.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0px 1px 10px 0px hsla(0deg, 0%, 0%, 0.12), 0px 2px 4px -1px hsla(0deg, 0%, 0%, 0.2);
  padding: 16px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  background: transparent;
  transition: all 0.4s cubic-bezier(0.1, 0.6, 0.2, 0.5);
}
.header.active {
  background: #141617;
  padding: 8px 32px;
  box-shadow: hsla(0deg, 0%, 0%, 0.14), 0px 5px 22px 4px hsla(0deg, 0%, 0%, 0.12), 0px 7px 8px -4px hsla(0deg, 0%, 0%, 0.2);
}
.header__logo img {
  width: 120%;
  height: auto;
}
.header__logo img.active {
  width: 50%;
  height: auto;
}
@media (max-width: 599px) {
  .header__logo img {
    width: 80%;
    height: auto;
  }
}
.header__navbar a {
  font-family: "Bruno Ace";
  font-size: 16px;
  text-transform: capitalize;
  margin: 0 16px;
  color: #fff;
  transition: 0.2s cubic-bezier(0.1, 0.6, 0.2, 0.5);
}
.header__navbar a:hover {
  color: #f2662c;
}
.header__navbar a.activee {
  border-bottom: 1px solid #f2662c;
  color: #f2662c;
}
@media (max-width: 1200px) {
  .header__navbar a {
    font-size: 30%;
    margin: 0 0px;
  }
}
@media (max-width: 900px) {
  .header__navbar a {
    font-family: "Bruno Ace";
    font-size: 16px;
    text-transform: capitalize;
    transition: all 0.2s linear;
    margin-bottom: 16px;
  }
}
@media (max-width: 900px) {
  .header__navbar {
    position: absolute;
    top: 99%;
    left: 0;
    right: 0;
    transition: 0.4s linear;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #141617;
    border-radius: 8px;
    padding: 24px 0;
    display: none;
  }
  .header__navbar.active {
    display: flex;
    animation: swing-in-top-fwd 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  }
  .header__navbar.in-active {
    display: flex;
    animation: slide-out-bck-top 0.5s cubic-bezier(0.6, -0.28, 0.735, 0.045) both;
  }
}
.header__icons div {
  height: 32px;
  width: 32px;
  font-size: 24px;
  color: #f2662c;
  cursor: pointer;
  text-align: center;
  margin-left: 8px;
}
.header__icons #menu-btn {
  display: none;
}
@media (max-width: 900px) {
  .header__icons #menu-btn {
    display: inline-block;
  }
}
@media (min-width: 901px) {
  .header {
    padding: 8px, 16px;
  }
}

.contact .heading {
  color: #fff;
}
.contact .row {
