  font-size: 2rem;
  text-transform: capitalize;
  margin-top: 2rem;
  padding-top: 1rem;
}
.footer .credit span {
  color: #f2662c;
}

.home {
  padding: 0;
}
.home__slide {
  min-height: 60rem;
  display: flex;
  align-items: center;
  position: relative;
  background-size: cover;
  background-position: center;
  padding: 4rem;
}
.home__slide::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(180deg, #141617, transparent);
}
@media (max-width: 900px) {
  .home__slide {
    justify-content: center;
  }
}
.home__content {
  width: 50rem;
  position: relative;
}
.home__content h3 {
  font-family: "Bruno Ace";
  color: #fff;
  font-family: "Bruno Ace";
  font-size: 6rem;
  text-transform: capitalize;
  text-transform: uppercase;
}
.home__content p {
  font-size: 1.7rem;
  line-height: 2;
  padding: 1rem 0;
}
@media (max-width: 900px) {
  .home__content {
    justify-content: center;
    text-align: center;
  }
  .home__content h3 {
    font-size: 3rem;
  }
}
.home__btn {
  transition: all 0.2s linear;
  font-size: 4rem;
  color: #000;
  padding: 2rem;
}
.home__btn:hover {
  color: #f2662c;
  transform: scale(1.1);
}
@media (max-width: 900px) {
  .home__btn {
    padding: 4rem;
  }
}

