  transition: 0.2s linear;
}
.services .box-container .box h3 {
  font-family: "Bruno Ace";
  font-size: 1.7rem;
  text-transform: capitalize;
  padding: 1rem 0;
}
.services .box-container .box p {
  font-size: 1.4rem;
  line-height: 2;
  padding: 1rem;
}

